.Header{border-bottom:1px solid #E5E5E5}.Header_Dropdown .ant-dropdown-link{padding-right:15px;position:relative}.Header_Dropdown .ant-dropdown-link:after{color:#707070;position:absolute;top:50%;right:0;margin-top:-2px;display:inline-block;width:0;height:0;margin-left:.255em;vertical-align:.255em;content:'';border-top:.3em solid;border-right:.3em solid transparent;border-bottom:0;border-left:.3em solid transparent}.Header_Dropdown .ant-dropdown-link:hover:after{color:#A1A1A1}.Header_Avatar{background:#E5E5E5 !important}

.RatioBox-1-1{padding-bottom:100%}.RatioBox-1-2{padding-bottom:50%}.RatioBox-1-3{padding-bottom:33.33333%}.RatioBox-9-16{padding-bottom:56.25%}

.Picture_Background img{background-color:#fff}.Picture_Contain img{object-fit:contain}.Picture_Contained img{width:100%;height:100%}.Picture_Cover img{object-fit:cover}.Picture_Radius img{border-radius:3px}.Picture_Scale-Down img{object-fit:scale-down}.Picture_Wide img{position:absolute;top:0;left:0;height:100%;width:100%;object-fit:cover}

.MenuLeft_Logo{height:64px}

.MainMenu_DarkDrawer .drawer-handle{background:#001529}.MainMenu_DarkDrawer .drawer-handle-icon{background:#fff}.MainMenu_DarkDrawer .drawer-handle-icon:after,.MainMenu_DarkDrawer .drawer-handle-icon:before{background:#fff}.MainMenu_LightDrawer .drawer-handle{background:#fff}.MainMenu_LightDrawer .drawer-handle-icon{background:#001529}.MainMenu_LightDrawer .drawer-handle-icon:after,.MainMenu_LightDrawer .drawer-handle-icon:before{background:#001529}

.StatisticCard{border:none;border-radius:10px;box-shadow:0 0 10px -4px}.StatisticCard_Icon{font-size:2rem}.StatisticCard_Title{font-size:.8rem}.StatisticCard_Value{font-size:1.2rem}

.GraphCard{border:none;border-radius:10px;box-shadow:0 0 10px -4px}.GraphCard_Title{font-size:.8rem}

